import {UniLoadMore} from 'components/uni/uni-load-more/uni-load-more'
import {GetUserCourseList} from 'api/user'
export default {
	components:{
		UniLoadMore
	},
	data() {
		return {
			tabList:[],
			index:0,
			list:[],
			costype:-1,
			page:1,
			pageCount:1,
			moreStatus:"",
		}
	},
	onPullDownRefresh() {
		this.getList() 
	},
	/*滚动加载*/
	onReachBottom(){
		this.page++
		if(this.page<=this.pageCount){
			this.getList(this.page)
		}	
	},
	onLoad() {
		if(!this.$store.state.isshowprice){
			this.tabList=[{
				name:'全部',
				value:-1
			},{
				name:'点播',
				value:1
			},{
				name:'面授',
				value:0
			}]
		}else{
			this.tabList=[{
				name:'全部',
				value:-1
			},{
				name:'点播',
				value:1
			},{
				name:'直播',
				value:5
			},{
				name:'面授',
				value:0
			}]
		}
		this.getList(); 
	},
	methods: {
		changeTabs(e){
			this.costype = e.tab.value
			this.getList()	
		},
		open(item){
			uni.navigateTo({
				url:'/pages/web/web?title='+item.name+'&photo='+item.photourl+'&url='+encodeURIComponent(item.view_url)
			})
		},
		/**请求课程列表*/
		getList(p=1){
			this.page = p
			if(this.page==1){
				this.list = []
			}
			this.moreStatus = 'loading'
			GetUserCourseList({
				costype: (this.index == 0 && -1)||(this.index == 1 && 1)||(this.index == 2 && 5)||(this.index == 3 && 0),
				page:this.page,
				pagesize:10,
			}).then(res=>{
				uni.stopPullDownRefresh();
				this.pageCount = res.pagecount
				if(this.page >= this.pageCount){
					this.moreStatus = 'noMore'
				}else{
					this.moreStatus = 'more'
				}
				if(res.result){
					this.list = this.list.concat(res.data)
				}
			})		
		}
	}
}
